.modal-list__item-title {
margin-bottom: 5px;

    font-style: normal;
font-weight: 600;
font-size: 20px;
line-height: 1.2;
text-align: center;

color: var(--modal-title-color);

@media screen and (min-width: 1280px) {
    font-size: 24px;
    margin-bottom: 7px;
    
}

}

.modal-list__item-text {
  font-family: Montserrat;
font-style: normal;
font-weight: normal;
font-size: 16px;
line-height: 1.25;
text-align: center;

color: var(--modal-text-color);

@media screen and (min-width: 1280px){
  font-size: 18px;
  line-height: 1.22;
}
}

.modal-list__item {
  margin-bottom: 10px;

  @media screen and (min-width: 1280px) {
    margin-bottom: 20px;
  }
}





  
  .modal {
    transform: translate(-50%, -50%) scale(1.2);
  }
  
  .backdrop {

    // display: none;
    position: fixed;
    top: 0;
    left: 0;
  
    width: 100vw;
    height: 100vh;
    background: rgba(32, 13, 29, 0.6);
backdrop-filter: blur(3px);
    opacity: 1;
    
  
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    z-index: 2;
  }
  
  // .backdrop.is-hidden .modal {
  //   transform: translate(-50%, -50%) scale(1.2);
  // }
  
  .backdrop.is-hidden {
      opacity: 0;
      visibility: 0;
      pointer-events: none;
    }
  
  

  .modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    min-width: 272px;
    max-height: 915px;
    background-color: var(--modal-background-color);
    
    border-radius: 150px 0px;
    padding: 75px 17px 104px 12px;

    @media screen and (min-width: 768px) {
      padding: 28px 17px 56px 50px;
      width: 595px;
max-height: 831px;
border-radius: 300px 0px;
    }

    @media screen and (min-width: 1280px) {
      padding: 62px 30px;
      width: 930px;
max-height: 1023px;
    }
  }


  
  .modal__button {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  
    margin-top: 25px;
    margin-right: 25px;
    padding: 0;
  
    width: 17px;
    height: 17px;
    border: none;
    
  
    
    cursor: pointer;
  }
  
.modal__img--preview {
margin-left: auto;
margin-right: auto;
border-radius: 50%;
widows: 102px;
height: 102px;
}

.modal__img {
  object-fit: cover;
  display: none;

  @media screen and (min-width: 768px) {
    display: block;
    max-width: 233px;
max-height: 416px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 360px;
max-height: 641px;
  }
}

.modal__content--flex {
  display: flex;
  margin-top: 18px;
  margin-bottom: 25px;

  @media screen and (min-width: 768px) {
    margin-top: 43px;
    margin-bottom: 43px;
  }

  @media screen and (min-width: 1280px) {
    margin-top: 41px;
    margin-bottom: 45px;
  }
}

.modal__content--description {
display: block;
text-align: center;

@media screen and (min-width: 768px){
  margin-left: 30px;
  text-align: left;
}

@media screen and (min-width: 1280px) {
  margin-left: 60px;
}
}





  // .modal__button:hover .modal__icon {
  //   fill: var(--primary-logo-color);
  // }
  
  .modal__icon {
    background-color: var(--modal-background-color);
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
    
  }